body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: transparent;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  
  overflow: hidden;
}



.header-container{
  display: flex;
  align-items: center;
  height: 800px;
  box-sizing: border-box;
  max-width: 1000px;
  position: relative;
  top: 150px;
}
.header-img{
  background-image: url(/static/media/Profile.9a417cc5.png);
  background-size: contain;
  background-repeat: no-repeat;
  position: relative;
  width: 100%;
  height: 100%;
  opacity: 1;
 
}
.header-text{
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100vw;
  padding: 2vw;
}
@media only screen and (max-width: 700px ){
  .header-img{
    position: absolute;
    width: 75%;
    opacity: 0.5;
    z-index: -20;
    background-size: contain;
    top: -20px;
  }
}
@media only screen and (min-width: 700px ){
  .header-img{
    opacity:1;
  }
}
nav{
    display: flex;
    list-style-type: none;
    width: 100vw;
    padding: 0;
    margin: 0;
    color: white;

}
.nav-item{
    float: left;
    background-color: #000000;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
    width: 100%;
    right: 0px;
    list-style-type: none;
    z-index: 100;
  position: fixed;
}
@media only screen and (max-width: 700px ){
  .nav-item>li{
    font-size: 2.5vw;
  }
}
.nav-item>li {
    
    text-align: center;
 
  text-decoration: none;
    cursor: pointer;
    width: 10vw;
  }


.project{
    display: flex;
    flex-direction: column;
    align-items: center;
    
    
    padding-top: 3%;
}
.proContainer{
    margin-top: 10%;
    
}
.g-container{
    justify-items: center;
    display: grid;
    grid-template-columns: auto;
    height: 600px;
    overflow-x: hidden;
    overflow-y: scroll;
    width: 55%;
    background: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);
    color: rgb(0, 0, 0);
    margin-bottom: 5%;
    z-index: 2;
    position: relative;
    }

.laptop{
    width: 83.3333333%;
    height: 775px;
    position: absolute;
    top:14%;
    left: 8.35%;
    z-index: 1;
}
@media only screen and (max-width: 1500px){
    .laptop{
        height: 640px;
        top: 16.2%;
    }
    .g-container{
        height: 500px;
    }
}
@media only screen and (max-width: 1200px){
    .laptop{
        height: 450px;
        top: 20.9%;
    }
    .g-container{
        height: 350px;
    }
}

@media only screen and (max-width: 900px ){
    .g-container{
        width: 66%;
    }
    .laptop{
        width: 100%;
        height: 400px;
        top: 22.2%;
        left: 0.1%
    }
    .g-container{
        height: 309px;
    }
    
}
@media only screen and (max-width: 600px ){
    .g-container{
        width: 80%;
    }
    .laptop{
        width: 122.5%;
        height: 350px;
        left:-11%;
        top: 23.4%
    }
    .g-container{
        height: 273px;
    }
}
.projectItem{
    font-size: 30px;
    width: 100%;
    margin-bottom: 5vh;
    
}
.proIMG:hover{
    scale: 1.05;
}
.image--section{
    display: grid;
    grid-template-columns: 12.5% 75% 12.5%;
    align-items: center;
    justify-items: center;
}
.prev{
    height: 100px;
    width: 50px;
    color: white;
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
    opacity: 0.5;
}
.next{
    height: 100px;
    width: 50px;
    color: white;
    opacity: 0.5;
}
.next:hover{
    scale: 1.1;
}
.prev:hover{
    scale: 1.1;
}
@media only screen and (max-width: 900px){
    .next{
        scale: 0.6
    }
    .next:hover{
        scale: 0.7
    }   
    .prev{
        scale: 0.6
    }
    .prev:hover{
        scale: 0.7
    }
}
.skills{
    margin-top: 5%;
    display: grid;
    grid-template-columns: auto auto auto;
    justify-items: center;
    grid-column-gap: 10vw;
    -webkit-column-gap: 10vw;
            column-gap: 10vw;
    grid-row-gap: 10vw;
    row-gap: 10vw;
    padding: 60px;
    width: 100%;
    
}
@media only screen and (max-width: 1300px){
    .skills{
        grid-template-columns: auto auto;
    }
}
@media only screen and (max-width: 800px){
    .skills{
        grid-template-columns: auto;
        grid-gap: 20vw;
        gap: 20vw;
    }
}
.skills-container{
    margin-top: 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    padding-top: 3%;
}
.programming{
    border: 1px solid black;
    padding: 20px;
    background-color: rgba(29, 29, 29, 0.8);
    box-shadow: 5px 3px 5px 0px rgba(0, 0, 0, 0.5);
    color: white;
    position: relative;
}
.programming:hover{
    scale:1.05;
    transition: scale 0.25s ease-in-out;
}
.title{
    text-transform: uppercase;
	position: absolute;
	top: -75px;
	height: 50px;
	color: #eee;
	background-color: rgba(29, 29, 29, 0.856);
	width: 100%;
	left: 0;
	padding: 15px 15px 0 0;
	text-align: end;
	font-weight: 600;
	z-index: 1;
	font-size: 35px;
	line-height: 35px;
	-webkit-transform: scaleY(1.65);
	        transform: scaleY(1.65);
    
}
.title:hover{
    background-color: #2187e7;

    transition: background-color 0.5s ease 0s;
}

.proCon{
    width: 300px;
    height: 20px;
    background-color: rgba(175, 164, 164, 0.473);
    padding-bottom: 0px;

    opacity: 0.8;
}
.proficiency{
    height: 100%;
    box-shadow: 0px 0px 5px 1px #2187e7;

}
.proCon:hover{
    -webkit-filter: brightness(3);
            filter: brightness(3);
    transition: -webkit-filter 0.5s ease 0s;
    transition: filter 0.5s ease 0s;
    transition: filter 0.5s ease 0s, -webkit-filter 0.5s ease 0s;
}

.education-container{
    display: flex;
    flex-direction: row;
    position: relative;
    padding: 15px;
    align-items: center;
}
p{
    font-size: 1.2rem;
}
.education{
    width: 80%;
    padding-top: 3%;

}
.textEdu{
    width: 38%;
}
@media only screen and (max-width:1000px){
    .education{
        width: 100%;
    }
    .education-container{
        width: 100%;
    }
    .textEdu{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
}
.textEdu > p > span{
    text-decoration: underline;
}
.contact{
    display: flex;
    margin-top: 5%;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px;
    color: white;
    background: linear-gradient(109.6deg, rgba(0, 0, 0, 0.93) 11.2%, rgb(63, 61, 61) 78.9%);
    width: 100%;
}

.name{
    display: block;
    width: 300px;
}
form{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-gap: 15px;
    gap: 15px;
}
form > input{
    padding-left: 10px;
}
.email{
    width: 500px;
}
.message{
    width: 700px;
    height: 200px;
    resize: none;
}
form > button:hover{
    scale: 1.08;
}
.message{
    padding-left: 10px;
}
.success{
    background-color:  #9ce47e;
    color: black;
    border-radius: 5px;
    padding: 10px;
    opacity: 0.7;
    width: 100%;
    font-weight: 550;
}
@media only screen and (max-width: 900px){
    .name{
        width: 200px;
    }
    .message{
        width: 500px;
    }
    .email{
        width: 300px;
    }
}
@media only screen and (max-width: 700px){
    .name{
        width: 200px;
    }
    .message{
        width: 400px;
    }
    .email{
        width: 300px;
    }
}
